export default {
  mounted: function () {
    this.consoleIndex()
  },
  methods: {
    onCancel: function () {
    },
    onConfirm: function (msg) {
      console.log('onConfirm', msg)
      switch (this.actionsheetStr) {
        case this.menusList.menu1:
          this.bzhblist.push({title: this.inputStr, contents: this.textareaStr, isshow: false})
          break
        case this.menusList.menu2:
          this.xzjhlist.push({title: this.inputStr, contents: this.textareaStr, isshow: false})
          break
        default:
          console.log('onConfirm', '错误')
          switch (this.index) {
            case 0:
              this.setList(this.bzhblist, this.groupId)
              break
            case 1:
              this.setList(this.xzjhlist, this.groupId)
              break
            case 2:
              this.grouplist = this.szhblist
              break
          }
          break
      }
      this.consoleIndex()
      console.log('onConfirm', this.inputStr)
      console.log('onConfirm', this.textareaStr)
    },
    setList: function (list, id) {
      list[id].title = this.inputStr
      list[id].contents = this.textareaStr
      list[id].isshow = true
    },
    onHide: function () {
    },
    onShow: function () {
    },
    inputonChange: function (val) {
      console.log('on change', val)
      this.inputStr = val
    },
    textareaonChange: function (val) {
      console.log('on change', val)
      this.textareaStr = val
    },
    actionsheetClick: function (key, item) {
      this.inputStr = ''
      this.textareaStr = ''
      this.showConfirm = true
      this.actionsheetStr = item
      this.confirmTitle = item
      console.log('actionsheetClick', key + item)
    },
    consoleIndex: function () {
      switch (this.index) {
        case 0:
          this.setGrouplist(this.bzhblist, this.list1)
          break
        case 1:
          this.setGrouplist(this.xzjhlist, this.list2)
          break
        case 2:
          this.setGrouplist(this.szhblist, [])
          break
      }
    },
    setGrouplist: function (list, btnlist) {
      this.grouplist = list
      this.flexboxlist = btnlist
      if (list.length > 0 && btnlist.length > 0) {
        this.divIsShow = true
      } else {
        this.divIsShow = false
      }
    },
    buttonClick: function (item, id) {
      console.log('consoleIndex', item.title)
      switch (item.title) {
        case '转下周':
          this.xzjhlist.push(this.bzhblist[id])
          this.bzhblist.splice(id)
          break
        case '完成':
          break
        case '删除':
          switch (this.index) {
            case 0:
              this.bzhblist.splice(id)
              break
            case 1:
              this.xzjhlist.splice(id)
              break
            case 2:
              this.szhblist.splice(id)
              break
          }
          break
      }
      this.consoleIndex()
    },
    pClick: function (item, id) {
      this.groupId = id
      this.inputStr = item.title
      this.textareaStr = item.contents
      this.actionsheetStr = ''
      this.confirmTitle = '重新编辑'
      this.showConfirm = true
    },
    submit: function () {
      console.log('submit', '上班周报')
    }
  },
  data: function () {
    return {
      back: require('../assets/back.png'),
      confirmTitle: '提示框',
      name: '部门周报',
      buttonlist: ['本周汇报', '下周计划', '上周汇报'],
      flexboxlist: [],
      list1: [{title: '转下周', type: 'primary'}, {title: '完成', type: 'primary'}, {title: '删除', type: 'warn'}],
      list2: [{title: '删除', type: 'warn'}],
      index: 0,
      grouplist: [],
      bzhblist: [{title: '智慧油城0', contents: '智慧油城智慧油城智慧油城智慧油城智慧油城智慧油城智慧油城智慧油城智慧油城智慧油城', isshow: true}],
      xzjhlist: [],
      szhblist: [],
      menusList: {
        menu1: '创建本周汇报',
        menu2: '创建下周计划'
      },
      showMenus: false,
      showConfirm: false,
      inputStr: '',
      textareaStr: '',
      actionsheetStr: '',
      groupId: -1,
      divIsShow: true
    }
  }

}



